


































































































































































import mpvuePicker from '../../../components/mpvue-picker/mpvuePicker.vue';
import mpvuePickerF from '../../../components/mpvue-picker/mpvuePicker.vue';
export default {
	components: {
		mpvuePicker,mpvuePickerF
	},
	data() {
		return {
			credit_id: '',
			temp_id:'',
			card: [],
			loading: false,
			show:true,
			repayTemp:[],
			bannerShow:false,
			is_dz:0,
			dz_fee:0,
			zx_area:0,
			themeColor: '#007AFF',
			mode: '',
			deepLength: 1,
			pickerValueDefault: [0],
			pickerValueArray: [],
			pickerSingleArray:[],
			province:'',
			province_name:'',
			city:'',
			city_name:'',
			pickerValueArrayF:[],
			pickerSingleArrayF:[],
			
		}
	},
	computed: {

	},
	onLoad(e) {
		if (e.credit_id && e.temp_id) {
			this.credit_id = e.credit_id;
			this.temp_id = e.temp_id;
		} else {
			uni.switchTab({
				url: '/pages/tabBar/card/card'
			})
		}
	},
	onShow() {
		this.getRepayTemp();
	},
	onBackPress() {},
	methods: {
		// 单列
		showSinglePicker() {
			this.pickerValueArray = this.pickerSingleArray
			this.mode = 'selector'
			this.deepLength = 1
			this.pickerValueDefault = [0]
			this.$refs.mpvuePicker.show()
		},
		showSinglePickerCity() {
			this.pickerValueArrayF = this.pickerSingleArrayF
			this.mode = 'selector'
			this.deepLength = 1
			this.pickerValueDefault = [0]
			this.$refs.mpvuePickerF.show()
		},
		onConfirm(e) {
				this.province = e.value[0];
				this.province_name = e.label;
				this.city = '';
				this.city_name = '';
				this.getCity(e.value[0]);
		},
		onConfirmF(e) {
				this.city = e.value[0];
				this.city_name = e.label;
		},
		onCancel(e) {
			console.log(e)
		},
		goDetailPage(url) {
			uni.navigateTo({
					url: url
			})
		},
		closeBanner: function() {
			this.bannerShow = false;
		},
		getTipZzj(){
			this.bannerShow = true;
		},
		switch2Change: function (e) {
			if(e.target.value){
				this.province = '';
				this.province_name = '';
				this.city = '';
				this.city_name = '';
				this.getProvince();
				this.zx_area = 1;
			}else{
				this.zx_area = 0;
			}
			
		},
		getProvince: function () {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const current = this.repayTemp.current;
			try {
				uni.showLoading();
				this.$http.post(this.websiteUrl + '/api/repay/getProvince', {
					openid,
					sessionKey,
					current
				}, (res) => {
					uni.hideLoading();
					if (res.data.code == 1) {
						this.pickerSingleArray = res.data.result;
					}else{
						uni.showModal({
							content: res.data.msg,
							showCancel: false
						});
					}
				});
			} catch (e) {
				uni.hideLoading();
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				});
			}
		},
		getCity: function (province) {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const current = this.repayTemp.current;
			try {
				uni.showLoading();
				this.$http.post(this.websiteUrl + '/api/repay/getCity', {
					openid,
					sessionKey,
					province,
					current,
					
				}, (res) => {
					uni.hideLoading();
					if (res.data.code == 1) {
						this.pickerSingleArrayF = res.data.result;
					}else{
						uni.showModal({
							content: res.data.msg,
							showCancel: false
						});
					}
				});
			} catch (e) {
				uni.hideLoading();
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				});
			}
		},
		submitRepayStep: function () {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const temp_id = this.temp_id;
			const credit_id = this.credit_id;
			const is_dz = this.is_dz;
			const that = this;
			
			const province = this.province;
			const city = this.city;
			const zx_area = this.zx_area;

			try {
				if(this.loading){
					return;
				}
				this.loading = true;
				
				this.$http.post(this.websiteUrl + '/api/repay/submitRepayStep', {
					openid,
					sessionKey,
					temp_id,
					credit_id,
					is_dz,
					province,
					city,
					zx_area
				}, (res) => {
						if(res.data.code==1){
							if(res.data.result.url != ''){
								that.loading = false;
								uni.navigateTo({
									url: '/pages/card/web-view/web-view?url='+res.data.result.url
								})
							}else{
								uni.showModal({
									content:res.data.msg,
									showCancel:false,
									success:function() {
										that.loading = false;
										uni.navigateTo({
											url: '/pages/card/credit/repayInfo?credit_id='+that.credit_id
										})
									}
								})
							}
						}else{
							uni.showModal({
								content:res.data.msg,
								showCancel:false,
								success:function() {
									that.loading = false;
								}
							})
						}						
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false,
					success:function() {
						that.loading = false;
					}
				});
			}
		},
		getRepayTemp() {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const credit_id = this.credit_id;
			const temp_id = this.temp_id;
			try {
				this.$http.post(this.websiteUrl + '/api/repay/getRepayTemp', {
					openid,
					sessionKey,
					credit_id,
					temp_id
				}, (res) => {
					if (res.data.code == 1) {
						this.card = res.data.result.creditCard;
						this.repayTemp = res.data.result.repayTemp.temp_body;
						if(res.data.result.repayTemp.temp_body.current == 1){
							this.is_dz = 1;
						}
						this.dz_fee = res.data.result.dz_fee;
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				})
			}
		},
		trigerCollapse() {
			this.show = !this.show;
		}
	},
	onBackPress() {
		if (this.$refs.mpvuePicker.showPicker) {
			this.$refs.mpvuePicker.pickerCancel();
			return true;
		}
		
		if (this.$refs.mpvuePickerF.showPicker) {
			this.$refs.mpvuePickerF.pickerCancel();
			return true;
		}

		if (this.show) {
			this.show = false;
			return true;
		}
	},
	onUnload() {
		if (this.$refs.mpvuePicker.showPicker) {
			this.$refs.mpvuePicker.pickerCancel()
		}
		
		if (this.$refs.mpvuePickerF.showPicker) {
			this.$refs.mpvuePickerF.pickerCancel()
		}
	}
}
